import React from "react";
import AdminLayout from "../components/layout/admin/AdminLayout.js";
import { useState, useEffect } from "react";
import axios from "axios";
import { Button, Input } from "antd";
const AdminReviews = () => {
  //state
  const [reviews, setReviews] = useState([]);
  const [employeeEmail, setEmployeeEmail] = useState("");
  const [searchedReviews, setSearchedReviews] = useState([]);
  const [hideMainReviews, setHideMainReviews] = useState("");
  const [hideSearchedReviews, setHideSearchedReviews] = useState('')
  //functions
  const findEmployeeReviews = async () => {
    setHideMainReviews("hidden");
    setHideSearchedReviews('')
    try {
      const foundReviews = await axios.post(
        "/api/v1/reviews/find-reviews",
        { email: employeeEmail }
      );
      setSearchedReviews(foundReviews.data);
    } catch (err) {
      console.log(err);
    }
  };
  //hooks
  useEffect(() => {
    const getAllReviews = async () => {
      try {
        const gottenReviews = await axios.get(
          "/api/v1/reviews/get-all-reviews"
        );
        setReviews(gottenReviews.data);
      } catch (err) {
        console.log(err);
      }
    };
    getAllReviews();
  }, []);
  return (
    <>
      <AdminLayout>
        <div className="mx-10 my-1 flex-row flex w-[20rem] gap-1">
          <p>Employee Email: </p>
          <Input
            onChange={(e) => {
              setEmployeeEmail(e.target.value);
            }}
          />
          <Button onClick={findEmployeeReviews}>Search</Button>
          <Button
            onClick={() => {
              setHideMainReviews("");
              setHideSearchedReviews('hidden')
            }}
          >
            Clear
          </Button>
        </div>
        {reviews.map((items) => (
          <>
            <div
              className={`bg-gray-600 my-1 mx-10 h-[10rem] rounded ${hideMainReviews}`}
            >
              <div className="mx-1">
                <p className="text-white">Email: {items.email}</p>
                <p className="text-white">Reviewing: {items.reviewing}</p>
                <p className="text-white">Review: {items.review}</p>
              </div>
            </div>
          </>
        ))}
{
searchedReviews.map((items)=>(
    <>
                <div
              className={`bg-gray-600 my-1 mx-10 h-[10rem] rounded ${hideSearchedReviews}`}
            >
              <div className="mx-1">
                <p className="text-white">Email: {items.email}</p>
                <p className="text-white">Reviewing: {items.reviewing}</p>
                <p className="text-white">Review: {items.review}</p>
              </div>
            </div>
    </>
))
}
      </AdminLayout>
    </>
  );
};
export default AdminReviews;