import React from 'react'
import EmployeeHeader from './EmployeeHeader.js'
import EmployeeFooter from './EmployeeFooter.js'
const EmployeeLayout = (props) =>{
    return(
        <>
        <EmployeeHeader/>
        {
            props.children
        }
        <EmployeeFooter/>
        </>
    )
}
export default EmployeeLayout;