import React from "react";
import {useState} from 'react'
import { useNavigate } from "react-router-dom";
import {message} from 'antd'
import axios from 'axios'
const AdminLogin = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('')
  const navigate = useNavigate();
  const hangleLogin = async ()=>{
    try{
      const admin = await axios.post('/api/v1/admin/admin-login',{email: email, password: password});
      if(!admin){
        message.error('Admin Not Found');
      }else{
        message.success('Logged In!');
        navigate('/admin-dash');
      }
    }catch(err){
      console.log(err)
      message.error('something went wrong!');
    }
  }
  return (
    <>
      <div className="z-50 bg-gray-600 h-10 flex justify-center items-center">
        <h1 className="text-white">Admin Login</h1>
      </div>
      <div className="h-[40rem] flex justify-center items-center ">
        <div className="bg-gray-600 h-[10rem] w-[20rem] flex flex-col justify-center rounded">
          <p className='text-white mx-1' >Admin Email: </p>
          <input className="rounded mx-1" onChange={(e)=>{setEmail(e.target.value)}}></input>
          <p className='text-white mx-1' type='password' >Password:</p>
          <input className="rounded mx-1" onChange={(e)=>{setPassword(e.target.value)}}></input>
          <button className='rounded px-1 py-1 text-center text-white hover:bg-gray-500 my-2 mx-10' onClick={hangleLogin}>Login</button>
        </div>
      </div>
      <div className="bg-gray-600 h-[6.1rem]">
        <h1 className="text-center text-white">Login</h1>
      </div>
    </>
  );
};
export default AdminLogin;
