import React from "react";
import { Link,useNavigate } from "react-router-dom";
const EmployeeHeader = () => {
  const navigate = useNavigate();
  const handleLogOut = () =>{
    localStorage.removeItem('employee');
    navigate('/');
  }
  return (
    <>
      <div className="flex flex-row z-50 bg-gray-600 h-10 items-center justify-between">
        <div className='mx-5'>
          <ul className="flex flex-row gap-3 text-white ">
            <li className="text-white px-1 py-1 text-xl font-bold">Employee</li>
            <li className="hover:bg-gray-500 rounded px-1 py-1"><Link to='/employee-dash'>Pending Reviews</Link></li>
            <li className="hover:bg-gray-500 rounded px-1 py-1"><Link to='/employee-sent'>Sent Reviews</Link></li>
          </ul>
        </div>
        <div>
            <button className='text-white mx-5 hover:bg-gray-500 rounded px-1 py-1' onClick={handleLogOut}>Logout</button>
        </div>
      </div>
    </>
  );
};
export default EmployeeHeader;
