import React from 'react'
import EmployeeLayout from '../components/layout/employees/EmployeeLayout.js'
import {useEffect, useState} from 'react'
import axios from 'axios'
const EmployeeSentReviews = () =>{
const [allReviews, setAllReviews] = useState([])
const data = JSON.parse(localStorage.getItem("employee"));
const email = data.data.Employee.email;
useEffect(()=>{
    const getReviews = async () =>{
        try{
            const gottenReviews = await axios.post('/api/v1/reviews/find-reviews', {email: email});
            setAllReviews(gottenReviews.data);
            console.log(allReviews)
        }catch(err){
            console.log(err);
        }
    }
    getReviews();
},[])
    return(
<>
<EmployeeLayout>
{
    allReviews.map((items)=>(
        <>
        <div className='bg-gray-600 my-1 w-[100rem] mx-5 rounded' >
        <p className='text-bold text-white mx-1'>Assignment: {items.reviewing}</p>
        <p className='text-white mx-1'>Review: {items.review}</p>
        <p className='text-white mx-1'>Stars: {items.stars}</p>
        </div>
        </>
    ))
}
</EmployeeLayout>
</>
    )
}
export default EmployeeSentReviews;