import React from "react";
import axios from 'axios'
import {useState} from 'react'
import {message} from 'antd'
import { useNavigate } from "react-router-dom";
const EmployeeLogin = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();
  const handleLogin = async () =>{
    try{
      const employee = await axios.post('/api/v1/employee/employee-login',{email: email, password: password});
      localStorage.setItem('employee', JSON.stringify(employee));
      navigate('/employee-dash');
      message.success('Logged In!');
    }catch(err){
      console.log(err)
      message.error('Something Went Wrong!');
      navigate('/');
    }
  }
  return (
    <>
      <div className="z-50 bg-gray-600 h-10 flex justify-center items-center">
        <h1 className="text-white">Employee Login</h1>
      </div>
      <div className="h-[40rem] flex justify-center items-center ">
        <div className="bg-gray-600 h-[10rem] w-[20rem] flex flex-col justify-center rounded">
          <p className="text-white mx-1">Email: </p>
          <input className="rounded mx-1" onChange={(e)=>{setEmail(e.target.value)}}></input>
          <p className="text-white mx-1">Password:</p>
          <input className="rounded mx-1" onChange={(e)=>{setPassword(e.target.value)}} type='password'></input>
          <button className="rounded px-1 py-1 text-center text-white hover:bg-gray-500 my-2 mx-10" onClick={handleLogin}>
            Login
          </button>
        </div>
      </div>
      <div className="bg-gray-600 h-[6.1rem]">
        <h1 className="text-center text-white">Login</h1>
      </div>
    </>
  );
};
export default EmployeeLogin;
