import React from "react";
import { Link } from "react-router-dom";
const Home = () => {
  return (
    <>
      <div className="z-50 bg-gray-600 h-10 flex justify-center items-center">
        <h1 className="text-white">Login</h1>
      </div>
      <div className="h-[40rem] flex justify-center items-center flex-row ">
        <div className="bg-gray-600 h-[10rem] w-[20rem] flex justify-center items-center gap-10 rounded">
          <Link to='/admin-login'>
          <button className="text-white hover:bg-gray-500 px-1 py-1 rounded">
            Admin Login
          </button>
          </Link>
          <Link to='/employee-login'>
          <button className="text-white hover:bg-gray-500 px-1 py-1 rounded">
            Employee Login
          </button>
          </Link>
        </div>
      </div>
      <div className="bg-gray-600 h-[6.1rem]">
        <h1 className="text-center text-white"></h1>
      </div>
    </>
  );
};
export default Home;
