import React from "react";
import AdminLayout from "../components/layout/admin/AdminLayout.js";
import axios from "axios";
import { useState, useEffect } from "react";
import { Form, Modal, Input, Button, message } from "antd";
import { DeleteOutlined } from "@ant-design/icons";
const AdminDash = () => {
  //add employee states
  const [allEmployees, setEmployees] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");
  const [assignment1, setAssignment1] = useState("");
  const [assignment2, setAssignment2] = useState("");
  const [assignment3, setAssignment3] = useState("");
  //update employee states
  const [showUpdateModal, setUpdateModal] = useState(false);
  const [updateEmail, setUpdateEmail] = useState("");
  const [updateName, setUpdateName] = useState("");
  const [updatePassword, setUpdatePassword] = useState("");
  const [updateAssignment1, setUpdateAssignment1] = useState("");
  const [updateAssignment2, setUpdateAssignment2] = useState("");
  const [updateAssignment3, setUpdateAssignment3] = useState("");
  //functions
  const handleModelOpen = () => {
    setShowModal(true);
  };
  const handleModelClose = () => {
    setShowModal(false);
  };
  const handleFormFinish = async () => {
    setShowModal(false);
    try {
      await axios.post("/api/v1/employee/add-employee", {
        email: email,
        name: name,
        password: password,
        assignment1: assignment1,
        assignment2: assignment2,
        assignment3: assignment3,
      });
      message.success("New Employee Added");
    } catch (err) {
      console.log(err);
      message.error("Something went wrong");
    }
  };
  const handleEmployeeDelete = async (value) => {
    try {
      await axios.post(
        "/api/v1/employee/delete-employee",
        { _id: value }
      );
      message.success("Employee Deleted!");
    } catch (err) {
      console.log(err);
      message.error("Something went wrong!");
    }
  };
  const handleUpdate = async (value) => {
    setUpdateModal(false);
    try {
      await axios.post(
        "/api/v1/employee/update-employee",
        {
          _id: value,
          payload: {
            email: updateEmail,
            password: updatePassword,
            name: updateName,
            assignment1: updateAssignment1,
            assignment2: updateAssignment2,
            assignment3: updateAssignment3,
          },
        }
      );
      message.success("Employee Updated!");
    } catch (err) {
      console.log(err);
      message.error("Failed");
    }
  };
  //hooks
  const [form] = Form.useForm();
  useEffect(() => {
    const getEmployees = async () => {
      const emps = await axios.get(
        "/api/v1/employee/get-employees"
      );
      setEmployees(emps.data);
    };
    getEmployees();
  }, [allEmployees]);
  return (
    <>
      <AdminLayout>
        <div className="flex justify-center items-center my-1 mx-1">
          <div className="w-[100rem] bg-gray-600 rounded grid grid-cols-5 h-[2rem] ">
            <div>
              <p className="text-white mx-1 font-bold">Employee Name</p>
            </div>
            <div>
              <p className="text-white font-bold">Employee Email</p>
            </div>
            <div>
              <ul className="text-white font-bold">
                <li>Employee Assignments</li>
              </ul>
            </div>
            <div>
              <DeleteOutlined className="text-white font-bold" />
            </div>
            <div>
              <p className="text-white font-bold">Update</p>
            </div>
          </div>
        </div>
        {allEmployees.map((items) => (
          <>
            <div className="flex justify-center items-center my-1 mx-1">
              <div className="w-[100rem] bg-gray-600 rounded grid grid-cols-5 h-[5rem] ">
                <div>
                  <p className="text-white mx-1" key={items.name}>
                    {items.name}
                  </p>
                </div>
                <div>
                  <p className="text-white " key={items.email}>
                    {items.email}
                  </p>
                </div>
                <div>
                  <ul className="text-white">
                    <li key={items.assignment1}>{items.assignment1}</li>
                    <li key={items.assignment2}>{items.assignment2}</li>
                    <li key={items.assignment3}>{items.assignment3}</li>
                  </ul>
                </div>
                <div>
                  <button
                    className="hover:bg-red-500 px-1 py-1 rounded"
                    onClick={() => {
                      handleEmployeeDelete(items._id);
                    }}
                  >
                    <DeleteOutlined className="text-white" />
                  </button>
                </div>
                <div>
                  <button
                    onClick={() => {
                      setUpdateModal(true);
                      setUpdateEmail(items.email);
                      setUpdateName(items.name);
                      setUpdatePassword(items.password);
                      setUpdateAssignment1(items.assignment1);
                      setUpdateAssignment2(items.assignment2);
                      setUpdateAssignment3(items.assignment3);
                      form.setFieldsValue({
                        email: items.email,
                        name: items.name,
                        password: items.passaword,
                        assignment1: items.assignment1,
                        assignment2: items.assignment2,
                        assignment3: items.assignment3,
                      });
                    }}
                  >
                    <p className="text-white hover:bg-gray-500 px-1 py-1 w-[3.7rem] rounded">
                      Update
                    </p>
                  </button>
                </div>
              </div>
            </div>
            <Modal
              open={showUpdateModal}
              title="Update Employee"
              footer={null}
              onCancel={() => {
                setUpdateModal(false);
              }}
            >
              <Form layout="vertical" form={form} onFinish={()=>{handleUpdate(items._id)}}>
                <Form.Item
                  label="Email"
                  name="email"
                  onChange={(e) => setUpdateEmail(e.target.value)}
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="Name"
                  name="name"
                  onChange={(e) => {
                    setUpdateName(e.target.value);
                  }}
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="Password"
                  name="password"
                  onChange={(e) => {
                    setUpdatePassword(e.target.value);
                  }}
                >
                  <Input.Password />
                </Form.Item>
                <Form.Item
                  label="Assignment 1"
                  name="assignment1"
                  onChange={(e) => {
                    setUpdateAssignment1(e.target.value);
                  }}
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="Assignment 2"
                  name="assignment2"
                  onChange={(e) => {
                    setUpdateAssignment2(e.target.value);
                  }}
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="Assignment 3"
                  name="assignment3"
                  onChange={(e) => {
                    setUpdateAssignment3(e.target.value);
                  }}
                >
                  <Input />
                </Form.Item>
                <Button type="default" htmlType="submit">
                  Done
                </Button>
              </Form>
            </Modal>
          </>
        ))}
        <div className="flex items-center justify-center my-1">
          <button
            className="text-white py-1 px-1 bg-gray-600 rounded hover:bg-gray-500"
            onClick={handleModelOpen}
          >
            Add Employee
          </button>
        </div>
        <Modal
          open={showModal}
          title="New Employee"
          footer={null}
          onCancel={handleModelClose}
        >
          <Form layout="vertical" onFinish={handleFormFinish}>
            <Form.Item
              label="Email"
              name="email"
              onChange={(e) => {
                setEmail(e.target.value);
              }}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Name"
              name="name"
              onChange={(e) => {
                setName(e.target.value);
              }}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Password"
              name="password"
              onChange={(e) => {
                setPassword(e.target.value);
              }}
            >
              <Input.Password />
            </Form.Item>
            <Form.Item
              label="Assignment 1"
              name="assignment1"
              onChange={(e) => {
                setAssignment1(e.target.value);
              }}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Assignment 2"
              name="assignment2"
              onChange={(e) => {
                setAssignment2(e.target.value);
              }}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Assignment 3"
              name="assignment3"
              onChange={(e) => {
                setAssignment3(e.target.value);
              }}
            >
              <Input />
            </Form.Item>
            <Button type="default" htmlType="submit">
              Done
            </Button>
          </Form>
        </Modal>
      </AdminLayout>
    </>
  );
};
export default AdminDash;
