import React from "react";
import EmployeeLayout from "../components/layout/employees/EmployeeLayout.js";
import { useState, useEffect } from "react";
import axios from "axios";
import { Form, Input, Button, message, Select } from "antd";
const EmployeeDash = () => {
  //state
  const [pendingReviews, setPendingReviews] = useState([]);
  const [review1, setReview1] = useState("");
  const [stars1, setStars1] = useState(0);
  const [review2, setReview2] = useState("");
  const [stars2, setStars2] = useState(0);
  const [review3, setReview3] = useState("");
  const [stars3, setStars3] = useState(0);
  //functions
  const handleFirstSubmit = async () => {
    try {
      await axios.post("/api/v1/reviews/new-review", {
        email: email,
        reviewing: pendingReviews.assignment1,
        review: review1,
        stars: stars1,
      });
      message.success("Review Sent!");
    } catch (err) {
      console.log(err);
      message.error("Something went wrong");
    }
  };
  const handleSecondSubmit = async () => {
    try {
      await axios.post("/api/v1/reviews/new-review", {
        email: email,
        reviewing: pendingReviews.assignment2,
        review: review2,
        stars: stars2,
      });
      message.success("Review Sent!");
    } catch (err) {
      console.log(err);
      message.error("Something went wrong");
    }
  };
  const handleThirdSubmit = async () => {
    try {
      await axios.post("/api/v1/reviews/new-review", {
        email: email,
        reviewing: pendingReviews.assignment3,
        review: review3,
        stars: stars3,
      });
      message.success("Review Sent!");
    } catch (err) {
      console.log(err);
      message.error("Something went wrong");
    }
  };
  //variabls
  const data = JSON.parse(localStorage.getItem("employee"));
  const email = data.data.Employee.email;
  //hooks
  useEffect(() => {
    const getPendingReviews = async () => {
      try {
        const PendingReviews = await axios.post(
          "/api/v1/employee/pending-reviews",
          { email: email }
        );
        setPendingReviews(PendingReviews.data);
      } catch (err) {
        console.log(err);
      }
    };
    getPendingReviews();
  }, [email]);
  return (
    <>
      <EmployeeLayout>
        <div className="bg-gray-300 my-5 mx-5 rounded">
          <Form layout="vertical" className="mx-1">
            <p className="text-bold">Assignment 1:</p>
            <p>Reviewing: {pendingReviews.assignment1}</p>
            <Form.Item name="review" label="Review:" className='w-[50rem]'>
              <Input onChange={(e)=>{setReview1(e.target.value)}}/>
            </Form.Item>
            <Form.Item name="stars" label="Stars:" className='w-[5rem]'>
              <Input onChange={(e)=>{setStars1(e.target.value)}}/>
            </Form.Item>
            <Button type="default" htmlType="submit" onClick={handleFirstSubmit}>
              Submit
            </Button>
          </Form>
        </div>
        <div className="bg-gray-300 my-5 mx-5 rounded">
          <Form layout="vertical" className="mx-1">
            <p className="text-bold">Assignment 2:</p>
            <p>Reviewing: {pendingReviews.assignment2}</p>
            <Form.Item name="review" label="Review:" className='w-[50rem]'>
              <Input onChange={(e)=>{setReview2(e.target.value)}}/>
            </Form.Item>
            <Form.Item name="stars" label="Stars:" className='w-[5rem]'>
            <Input onChange={(e)=>{setStars2(e.target.value)}}/>
            </Form.Item>
            <Button type="default" htmlType="submit" onClick={handleSecondSubmit}>
              Submit
            </Button>
          </Form>
        </div>
        <div className="bg-gray-300 my-5 mx-5 rounded">
          <Form layout="vertical" className="mx-1">
            <p className="text-bold">Assignment 3:</p>
            <p>Reviewing: {pendingReviews.assignment3}</p>
            <Form.Item name="review" label="Review:" className='w-[50rem]'>
              <Input onChange={(e)=>{setReview3(e.target.value)}}/>
            </Form.Item>
            <Form.Item name="stars" label="Stars:" className='w-[5rem]'>
            <Input onChange={(e)=>{setStars3(e.target.value)}}/>
            </Form.Item>
            <Button type="default" htmlType="submit" onClick={handleThirdSubmit}>
              Submit
            </Button>
          </Form>
        </div>
      </EmployeeLayout>
    </>
  );
};
export default EmployeeDash;
