import React from "react";
import { Link, useNavigate } from "react-router-dom";
import {message} from 'antd'
const AdminHeader = () => {
  const navigate = useNavigate();
  const handleLogout = () =>{
    message.success("Logged Out!");
    navigate('/');
  }
  return (
    <>
      <div className="flex flex-row z-50 bg-gray-600 h-10 items-center justify-between">
        <div className='mx-5'>
          <ul className="flex flex-row gap-3 text-white ">
            <li className="text-white px-1 py-1 text-xl font-bold">Admin</li>
           
            <li className="hover:bg-gray-500 rounded px-1 py-1"> <Link to='/admin-dash'>Employees </Link></li>
           
            <li className="hover:bg-gray-500 rounded px-1 py-1"><Link to='/admin-reviews'>Reviews</Link></li>
          </ul>
        </div>
        <div>
            <p className='text-white mx-5 hover:bg-gray-500 rounded px-1 py-1' onClick={handleLogout}>Logout</p>
        </div>
      </div>
    </>
  );
};
export default AdminHeader;
